<template>
  <div class="layui-card">
    <div class="layui-card-body">
      <form class="layui-form layui-form-pane">
        <div class="layui-form-item" pane>
          <div class="layui-form-label">
            
          </div>
          <div class="layui-input-block">
            <textarea class="layui-textarea" value="" style="height: 38px;min-height: 38px"></textarea>
          </div>
        </div>

        <template v-for="(item ,index) in data.accuser_evidence" :key="index">
          <div class="layui-form-item" pane>
            <div class="layui-inline" style="width: 100%;margin-bottom:0px;height: 38px;">
              <label class="layui-form-label"> {{ numberToChinese(item.serial) }}</label>
              <div class="layui-input-block">
                <div class="layui-input-inline" style="width:30%;margin-left: 0">
                  <VueMultiselect :option-height="38" :show-labels="false"
                                  v-model="data.accuser_evidence[index].evidence_type"
                                  :options="['','']" placeholder=""
                                  style="line-height: 16px;width: 100%; min-height: 38px;"></VueMultiselect>
                </div>
                <div class="layui-input-inline" style="width:70%;margin-left: 0">
                  <input type="text" placeholder="" class="layui-input"
                         v-model="data.accuser_evidence[index].evidence">
                </div>
              </div>
            </div>
          </div>

          <div class="layui-form-item" pane style="margin-top: -10px;">
            <div class="layui-form-label divcenter">
              
            </div>
            <div class="layui-input-block">
              <textarea type="text" v-model="data.accuser_evidence[index].content" placeholder=""
                        class="layui-textarea"></textarea>
            </div>
          </div>

          <div class="layui-form-item" style="width:100%;margin-top: -10px;">
            <button type="button" class="layui-btn layui-btn-radius layui-btn-xs"
                    @click="add_component('accuser_evidence',index+1)"> 
            </button>
            <button type="button" class="layui-btn layui-btn-radius layui-btn-danger layui-btn-xs"
                    v-if="index!=0"
                    style="margin-left:5px" @click="delete_component('accuser_evidence',index)"> 
            </button>
          </div>
        </template>

      </form>
    </div>
  </div>

  <fieldset class="layui-elem-field layui-field-title" id="defendant_and_other_accuser_query" style="margin-top:28px">
    <legend></legend>
    <div class="layui-field-box">
      <div class="layui-card">
        <div class="layui-card-body">
          <form class="layui-form layui-form-pane">
            <div class="layui-form-item" pane>
              <div class="layui-form-label ">
                
              </div>
              <div class="layui-input-block">
                <textarea class="layui-textarea" style="height: 38px;min-height: 38px"></textarea>
              </div>
            </div>

            <template v-for="(item,index) in data.defendant_and_other_accuser_query" :key="index">
              <div class="layui-form-item" pane>
                <div class="layui-form-label">
                  
                </div>
                <div class="layui-inline" style="width: 100%;margin-bottom:0px;">
                  <div class="layui-input-block">
                    <div class="myselect-div">
                      <VueMultiselect :option-height="38" :show-labels="false"
                                      v-model="data.defendant_and_other_accuser_query[index].name"
                                      :options=getCrossExaminationName placeholder=""
                                      :multiple="true"
                                      :close-on-select="false"
                                      :searchable="false"
                                      style="line-height: 16px; min-height: 38px;float:left;"></VueMultiselect>
                    </div>
                  </div>
                </div>
              </div>

              <div class="layui-form-item" pane style="margin-top: -10px;">
                <div class="layui-form-label">
                  
                </div>
                <div class="layui-inline" style="width: 100%;margin-bottom:0px;">
                  <div class="layui-input-block">
                    <div class="myselect-div">
                      <VueMultiselect :option-height="38" :show-labels="false"
                                      v-model="data.defendant_and_other_accuser_query[index].evidence"
                                      :options=getProofPlaintiff placeholder=""
                                      group-label="evidence"
                                      :group-select="true"
                                      group-values="option_label"
                                      :multiple="true"
                                      :close-on-select="false"
                                      :searchable="false"
                                      style="line-height: 16px; min-height: 38px;float:left;"></VueMultiselect>
                    </div>
                  </div>
                </div>
              </div>

              <div class="layui-form-item" pane style="margin-top: -10px;">
                <div class="layui-form-label">
                  
                </div>
                <div class="layui-input-block">
                  <div class="layui-input-inline" style="width:33%;margin-left: 0">
                    <div class="myradiomargin">
                      <label></label>
                      <input type="radio" lay-ignore value="1"
                             v-model="data.defendant_and_other_accuser_query[index].facticity"
                             class="myradio"><label></label>
                      <input type="radio" lay-ignore value="2"
                             v-model="data.defendant_and_other_accuser_query[index].facticity"
                             class="myradio" style="margin-left: 15px;"><label></label>
                    </div>
                  </div>
                  <div class="layui-input-inline" style="width:33%;margin-left: 0">
                    <div class="myradiomargin">
                      <label></label>
                      <input type="radio" lay-ignore value="1"
                             v-model="data.defendant_and_other_accuser_query[index].legality"
                             class="myradio"><label></label>
                      <input type="radio" lay-ignore value="2"
                             v-model="data.defendant_and_other_accuser_query[index].legality"
                             class="myradio" style="margin-left: 15px;"><label></label>
                    </div>
                  </div>
                  <div class="layui-input-inline" style="width:34%;margin-left: 0">
                    <div class="myradiomargin">
                      <label></label>
                      <input type="radio" lay-ignore value="1"
                             v-model="data.defendant_and_other_accuser_query[index].relevance"
                             class="myradio"><label></label>
                      <input type="radio" lay-ignore value="2"
                             v-model="data.defendant_and_other_accuser_query[index].relevance"
                             class="myradio" style="margin-left: 15px;"><label></label>
                    </div>
                  </div>
                </div>
              </div>

              <div class="layui-form-item" pane style="margin-top: -10px;">
                <div class="layui-form-label" style="line-height: 80px">
                  
                </div>
                <div class="layui-input-block">
              <textarea v-model="data.defendant_and_other_accuser_query[index].fact_reason"
                        placeholder=""
                        class="layui-textarea"></textarea>
                </div>
              </div>

              <div class="layui-form-item" style="width:100%;margin-top: -10px;">
                <button type="button" class="layui-btn layui-btn-radius layui-btn-xs"
                        @click="add_component('defendant_and_other_accuser_query',index+1)"> 
                </button>
                <button type="button" class="layui-btn layui-btn-radius layui-btn-danger layui-btn-xs"
                        style="margin-left:5px" v-if="index!=0"
                        @click="delete_component('defendant_and_other_accuser_query',index)"> 
                </button>
              </div>
            </template>

            <div class="layui-form-item" pane>
              <label class="layui-form-label"></label>
              <div class="layui-input-block">
                <div class="myradiomargin">
                  <input type="radio" name="is_defendant_evidence" lay-ignore v-model="setIsDefendantEvidence"
                         class="myradio"
                         value="1"><label> </label>
                  <input type="radio" name="is_defendant_evidence" lay-ignore v-model="setIsDefendantEvidence"
                         class="myradio" value="2"
                         style="margin-left: 15px;"><label></label>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </fieldset>

</template>

<script>

import VueMultiselect from "vue-multiselect";

export default {

  data() {
    var data;
    data = {
      //json accuser_evidence 3
      accuser_evidence: [{
        serial: 1,//
        evidence: "", //( )
        content: "", //( )
        evidence_type: ""//
      }],
      // accuser_evidence_fact_reason: "", //()
      defendant_and_other_accuser_query: [
        {
          name: [], //
          evidence: [], //
          fact_reason: "", //()
          facticity: "1", //
          legality: "1", //
          relevance: "1", //
        }
      ],
      is_defendant_evidence: "2",
    }
    var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
    if (wholeItem != null && "accuserShowInfo" in wholeItem) {
      data = wholeItem.accuserShowInfo
    }
    return {data: data}
  },
  mount() {

  },
  computed: {

    // 
    getCrossExaminationName: {
      get() {
        let defendant = this.$store.state.defendant_item.map(e => (e.defendant_short == '' ? e.defendant : e.defendant_short)+"").filter(i => i && i!= "")
        let accuser = this.$store.state.plaintiff_item.map(e => (e.accuser_short == '' ? e.accuser : e.accuser_short)+"").filter(i => i && i!= "")
        let thirdparty = this.$store.state.third_party_item.map(e => (e.third_party_short == '' ? e.third_party : e.third_party_short)+"").filter(i => i && i!= "")
        return accuser.concat(defendant).concat(thirdparty)
      }
    },
    getAccuserName: {
      get() {
        return this.$store.state.plaintiff_item.map(e => e.accuser_short == '' ? e.accuser : e.accuser_short).filter(i => i && i.trim())
      }
    },
    //
    getProofPlaintiff: {
      get() {
        let option_label = this.data.accuser_evidence.filter(i => i.evidence).map(e => {
          return "" + this.numberToChinese(e.serial) + ":" + e.evidence;
        })
        // let optionn_label={evidence_serial:evidence_serial,name_serial:name_serial}

        return [{evidence: "", option_label: option_label}]
      },
    },
    setIsDefendantEvidence:{
      get() {
        return this.$store.state.is_defendant_evidence
      },
      set(value) {
        this.data.is_defendant_evidence = value
        this.$store.commit('setIsDefendantEvidence', value )
      }
    }
  },
  components: {
    VueMultiselect
  },
  methods: {
    add_component(datatype, index) {
      switch (datatype) {
        case "accuser_evidence":
          //accuser_evidence 
          // this.data.accuser_evidence.push({
          //   serial: this.data.accuser_evidence.length+1,//
          //   evidence: "", //( )
          //   content: "", //( )
          //   evidence_type: ""//
          // });
          this.data.accuser_evidence.splice(index, 0, {
            serial: 0,//
            evidence: "", //( )
            content: "", //( )
            evidence_type: ""//
          })
          for (let i = 0; i < this.data.accuser_evidence.length; i++) {
            this.data.accuser_evidence[i].serial = i + 1
          }
          break;
        case "defendant_and_other_accuser_query":
          //
          this.data.defendant_and_other_accuser_query.splice(index,0,{
            name: [], //
            evidence: [], // 
            facticity: "1", //
            legality: "1", //
            relevance: "1", //
            fact_reason: "", //
          });
          break;

        default:
          //
          break;
      }
    },
    delete_component(datatype, index) {
      switch (datatype) {
        case "accuser_evidence":
          //
          this.data.accuser_evidence.splice(index, 1);
          for (let i = 0; i < this.data.accuser_evidence.length; i++) {
            this.data.accuser_evidence[i].serial = i + 1
          }
          break;
        case "defendant_and_other_accuser_query":
          //
          this.data.defendant_and_other_accuser_query.splice(index, 1);
          break;

        default:
          //
          break;
      }
    },
    numberToChinese(num) {
      var AA = new Array("", "", "", "", "", "", "", "", "", "", "");
      var BB = new Array("", "", "", "", "", "", "", "");
      var a = ("" + num).replace(/(^0*)/g, "").split("."),
          k = 0,
          re = "";
      for (var i = a[0].length - 1; i >= 0; i--) {
        switch (k) {
          case 0:
            re = BB[7] + re;
            break;
          case 4:
            if (!new RegExp("0{4}//d{" + (a[0].length - i - 1) + "}$")
                .test(a[0]))
              re = BB[4] + re;
            break;
          case 8:
            re = BB[5] + re;
            BB[7] = BB[5];
            k = 0;
            break;
        }
        if (k % 4 == 2 && a[0].charAt(i + 2) != 0 && a[0].charAt(i + 1) == 0)
          re = AA[0] + re;
        if (a[0].charAt(i) != 0)
          re = AA[a[0].charAt(i)] + BB[k % 4] + re;
        k++;
      }
      if (a.length > 1) // ()
      {
        re += BB[6];
        for (i = 0; i < a[1].length; i++)
          re += AA[a[1].charAt(i)];
      }
      if (re == '')
        re = "";
      if (re.match(/^/) && re.length == 3)
        re = re.replace("", "");
      return re;
    }

  },
  watch: {
    data: {
      handler() {
        //
        if (this.$store.state.court_number == "") {
          // window.layui.layer.msg('');
        } else {
          var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
          if (wholeItem != null) {
            wholeItem.accuserShowInfo = this.data
            localStorage.setItem(this.$store.state.court_number, JSON.stringify(wholeItem))
          }
        }
      },
      deep: true
    }
  },
};
</script>


<style type="text/css">
.layui-table-cell {
  height: auto;
  line-height: 28px;
  margin-top: 15px;
}
</style>
